.tab_button {
  padding: 0.5rem 0.75rem;
  border-radius: 3rem;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin-right: 0.5rem;
  display: inline-block;
  position: relative;
  top: -0.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.tab_button:hover {
    background-color: #EEEEEE;
}
.tab_button:active {
    background-color: #dcdcdc;
}
.tab_button::after {
    content: '';
    position: absolute;
    background-color: #42A7FF;
    width: calc(100% - 1rem);
    left: 0.5rem;
    bottom: -0.3rem;
    height: 0.2rem;
    opacity: 0;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
}
.tab_button--selected {
    cursor: default;
    font-weight: bold;
}
.tab_button--selected:active, .tab_button--selected:hover {
      background-color: transparent;
}
.tab_button--selected::after {
      opacity: 1;
}
