  cursor: pointer;
  font-family: "Lato", sans-serif;
  font-size: 2rem;
  font-weight: 300;
  text-align: center;
  border-radius: 100%;
  background-color: #BDBDBD;
  color: #fff;
}
.avatar_icon__icon--small {
    height: 2.5rem;
