.shadow_border {
  -webkit-box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25);
          box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25);
}
.shadow_border--hover {
  -webkit-box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25), 0 0.2rem 0.35rem rgba(175, 175, 175, 0.25);
          box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25), 0 0.2rem 0.35rem rgba(175, 175, 175, 0.25);
}
.tab_button {
  padding: 0.5rem 0.75rem;
  border-radius: 3rem;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin-right: 0.5rem;
  display: inline-block;
  position: relative;
  top: -0.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.tab_button:hover {
    background-color: #EEEEEE;
}
.tab_button:active {
    background-color: #dcdcdc;
}
.tab_button::after {
    content: '';
    position: absolute;
    background-color: #42A7FF;
    width: calc(100% - 1rem);
    left: 0.5rem;
    bottom: -0.3rem;
    height: 0.2rem;
    opacity: 0;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
}
.tab_button--selected {
    cursor: default;
    font-weight: bold;
}
.tab_button--selected:active, .tab_button--selected:hover {
      background-color: transparent;
}
.tab_button--selected::after {
      opacity: 1;
}
@-webkit-keyframes loading {
0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
}
50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
}
100% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
}
}
@keyframes loading {
0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
}
50% {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
}
100% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
}
}
.loading_icon {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  pointer-events: none;
}
.loading_icon--dark span {
    background-color: #757575 !important;
}
.loading_icon span {
    height: 0.5rem;
    width: 0.5rem;
    display: inline-block;
    border-radius: 100%;
    background-color: rgba(255, 255, 255, 0.9);
    -webkit-animation-name: loading;
            animation-name: loading;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    margin: 0 0.25rem;
}
.loading_icon span:nth-child(2n) {
      -webkit-animation-delay: 0.333s;
              animation-delay: 0.333s;
}
.loading_icon span:nth-child(3n) {
      -webkit-animation-delay: 0.666s;
              animation-delay: 0.666s;
}



