
@-webkit-keyframes flash {
0% {
    background-color: #E0E0E0;
}
50% {
    background-color: #E0E0E0;
}
75% {
    background-color: #EEEEEE;
}
to {
    background-color: #E0E0E0;
}
}
@keyframes flash {
0% {
    background-color: #E0E0E0;
}
50% {
    background-color: #E0E0E0;
}
75% {
    background-color: #EEEEEE;
}
to {
    background-color: #E0E0E0;
}
}
.shadow_border {
  -webkit-box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25);
          box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25);
}
.shadow_border--hover {
  -webkit-box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25), 0 0.2rem 0.35rem rgba(175, 175, 175, 0.25);
          box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25), 0 0.2rem 0.35rem rgba(175, 175, 175, 0.25);
}
.tab_button {
  padding: 0.5rem 0.75rem;
  border-radius: 3rem;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin-right: 0.5rem;
  display: inline-block;
  position: relative;
  top: -0.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.tab_button:hover {
    background-color: #EEEEEE;
}
.tab_button:active {
    background-color: #dcdcdc;
}
.tab_button::after {
    content: '';
    position: absolute;
    background-color: #42A7FF;
    width: calc(100% - 1rem);
    left: 0.5rem;
    bottom: -0.3rem;
    height: 0.2rem;
    opacity: 0;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
}
.tab_button--selected {
    cursor: default;
    font-weight: bold;
}
.tab_button--selected:active, .tab_button--selected:hover {
      background-color: transparent;
}
.tab_button--selected::after {
      opacity: 1;
}
.info_tooltip {
  position: relative;
}
.info_tooltip__content {
    opacity: 0;
    max-height: 7.5rem;
    border-radius: 0.25rem;
    pointer-events: none;
    width: 17.5rem;
    z-index: 2;
    overflow-y: auto;
    position: absolute;
    bottom: calc(100% + -0.5rem);
    background-color: #fff;
    padding: 0.5rem;
    border: 0.125rem solid #E0E0E0;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s;
}
.info_tooltip__content--show {
      bottom: calc(100% + 0.5rem);
      -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.03), 0 3px 6px rgba(0, 0, 0, 0.06);
              box-shadow: 0 3px 6px rgba(0, 0, 0, 0.03), 0 3px 6px rgba(0, 0, 0, 0.06);
      opacity: 1;
      display: initial;
      -webkit-transition: all 0.2s;
      transition: all 0.2s;
      -webkit-transition-delay: 0.5s;
              transition-delay: 0.5s;
}
.info_tooltip__content--pointer_events {
      pointer-events: all;
}
.info_tooltip__display {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: baseline;
        -ms-flex-align: baseline;
            align-items: baseline;
    cursor: pointer;
}



