.shadow_border {
  -webkit-box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25);
          box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25);
}
.shadow_border--hover {
  -webkit-box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25), 0 0.2rem 0.35rem rgba(175, 175, 175, 0.25);
          box-shadow: 0 0 0.3rem rgba(175, 175, 175, 0.25), 0 0.2rem 0.35rem rgba(175, 175, 175, 0.25);
}
.tab_button {
  padding: 0.5rem 0.75rem;
  border-radius: 3rem;
  cursor: pointer;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin-right: 0.5rem;
  display: inline-block;
  position: relative;
  top: -0.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.tab_button:hover {
    background-color: #EEEEEE;
}
.tab_button:active {
    background-color: #dcdcdc;
}
.tab_button::after {
    content: '';
    position: absolute;
    background-color: #42A7FF;
    width: calc(100% - 1rem);
    left: 0.5rem;
    bottom: -0.3rem;
    height: 0.2rem;
    opacity: 0;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
}
.tab_button--selected {
    cursor: default;
    font-weight: bold;
}
.tab_button--selected:active, .tab_button--selected:hover {
      background-color: transparent;
}
.tab_button--selected::after {
      opacity: 1;
}
.loading_button {
  position: relative;
}
.loading_button__slot {
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    opacity: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
}
.loading_button__icon {
    position: absolute;
    width: calc(100% - 1rem);
    opacity: 0;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    height: calc(100% - 0.85rem);
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.loading_button--loading {
  cursor: not-allowed;
}
.loading_button--loading .loading_button__icon {
    opacity: 1;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
.loading_button--loading .loading_button__slot {
    opacity: 0;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}



